.sommaire-item {
  padding: 0.3rem 0;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.2);
}

.sommaire-item button {
  border: none;
  outline: none;
  background: none;
  padding: 0;
  margin: 0;
  display: block;
  font-weight: 400;
  line-height: 14px;
  font-size: 0.8rem;
}

.sommaire-item button p {
  margin: 0;
  color: rgba(0, 0, 0, 0.5);
}
.sommaire-item button p.active {
  color: #0fd79c;
}
