/* Cards List */
.cards-list {
  position: relative;
  height: 100%;
  width: 100%;
}
.cards-end {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  position: absolute;
  background: white;
  width: 65%;
  height: 90%;
  border-radius: 6px;
  cursor: pointer;
  box-shadow: none;
}
.card-shadow {
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.25);
}
.card.inactive {
  visibility: hidden;
}

@media (min-height: 800px) {
  .card {
    width: 70%;
    height: 80%;
  }
}
