.videos__video.active {
  border-bottom: 1px solid #e9e9e9;
}
.videos__video-info {
  position: relative;
  display: flex;
  height: 4vh;
  bottom: 1px;
  justify-content: space-between;
  padding: 0.3rem 0.5rem;
  align-items: center;
}

.videos__video-info p {
  font-weight: 400;
  line-height: 16px;
  font-size: 1rem;
  margin: 0;
  color: #404040;
}
.btn {
  border: none;
  outline: none;
  background: transparent;
  margin: 0 0.7rem 0 0;
  padding: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.btn.sommaire svg path {
  fill: #0fd79c;
}

@media (min-height: 720px) {
  .videos__video-info p {
    font-size: 1.1rem;
  }
}
@media (min-height: 790px) {
  .videos__video-info p {
    font-size: 1.2rem;
  }
}
