.sommaire-video {
  position: absolute;
  background: white;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0.5rem;
  border-top: 1px solid #ff8515;
}

.title-bar {
  position: absolute;
  width: 75%;
  border-top: 21px solid #ff8515;
  border-bottom: 21px solid transparent;
  border-right: 21px solid transparent;
  height: 0;
  top: 0;
  left: 0;
}

.sommaire-video h3 {
  position: absolute;
  top: 0;
  left: 0;
  font-weight: 700;
  color: white;
  font-size: 0.7rem;
  margin: 0.25rem 1rem;
}
