/*  */
.react-card-flip {
  width: 100%;
  height: 100%;
}
.card-item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.25);
}

.card-item h1 {
  text-align: center;
  margin-bottom: 0;
}

.card-item .content {
  flex-grow: 1;
  padding: 0.5rem;
}

.card-item .content img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: fill;
}
