.videos {
  width: 100%;
  position: fixed;
  background: white;
  z-index: 50;
}

.videos__video-control {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  height: 43vh;
  background: white;
}

.videos__video-player iframe {
  width: 100%;
  height: 30vh;
}
.videos__sommaire {
  position: relative;
  top: 43vh;
}

.videos__menu {
  background: #137179;
  color: white;

  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 8vh;
}

.videos__menu-action {
  position: absolute;
  left: 0;
  cursor: pointer;
  padding: 0;
}
.videos__menu p {
  margin: 0;
}

@media (min-height: 680px) {
  .videos__video-control {
    height: 42.5vh;
  }

  .videos__sommaire {
    top: 42.5vh;
    position: relative;
  }
} ;
