.slide-in-left-enter {
  transform: translateX(-100%);
}
.slide-in-left-enter-active {
  transform: translateX(0);
  transition: 0.4s;
}
.slide-in-left-exit {
  transform: translateX(0);
}
.slide-in-left-exit-active {
  transform: translateX(-100%);
  transition: 0.4s;
}

.slide-videos-enter {
  transform: translateX(100%);
}
.slide-videos-enter-active {
  transform: translateX(0%);
  transition: 0.4s;
}
.slide-videos-exit {
  transform: translateX(0%);
  transition: 0.4s;
}
.slide-videos-exit-active {
  transform: translateX(100%);
  transition: 0.4s;
}

.slide-sommaire-videos-enter {
  transform: translateX(100%);
}
.slide-sommaire-videos-enter-active {
  transform: translateX(0%);
  transition: 0.5s;
}
.slide-sommaire-videos-exit {
  transform: translateX(0%);
  transition: 0.5s;
}
.slide-sommaire-videos-exit-active {
  transform: translateX(100%);
  transition: 0.5s;
}

.slide-quiz-enter {
  transform: translateY(100%);
}
.slide-quiz-enter-active {
  transform: translateY(0%);
  transition: 0.5s;
}
.slide-quiz-exit {
  transform: translateY(0%);
  transition: 0.5s;
}
.slide-quiz-exit-active {
  transform: translateY(100%);
  transition: 0.5s;
}
