/* Video Item */
.video-item {
  padding: 0.5rem 0 1rem 0.5rem;
  display: flex;
  border-bottom: 1px solid #e9e9e9;
}
.video-item__info {
  order: 2;
  flex-grow: 1;
  width: 100%;
}
.video-item__thumbnail {
  order: 1;
  flex-grow: 1;
}

.video-item__info h2 {
  font-size: 0.9rem;
  font-weight: 400;
  line-height: 0.8rem;
}

.video-item__info h2:last-child {
  color: rgba(0, 0, 0, 0.5);
}

.video-item__info.active h2:first-child {
  color: #0fd79c;
  font-weight: bold;
}

.video-item__thumbnail {
  display: block;
  border: none;
  width: 100%;
  outline: none;
  text-align: left;
  margin: 0 1.5rem 0 0;
  color: white;
  padding: 0.5rem 1rem;
  height: 15vh;
  position: relative;
  border-radius: 0px 3px 3px 0px;
  cursor: pointer;
  overflow: hidden;
}

.video-item__thumbnail:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    270deg,
    rgba(15, 215, 156, 0.87) 0%,
    rgba(255, 255, 255, 0) 100%
  );
}
.video-item__thumbnail h1 {
  position: absolute;
  top: 0.5rem;
  text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.32);
  font-size: 1.2rem;
  line-height: 1.3rem;
  margin: 0;
}

.video-item__thumbnail-play {
  width: 20px;
  height: 30px;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translateX(-50%);
}

.video-item__thumbnail-lessons {
  position: absolute;
  right: -1px;
  font-weight: 700;
  bottom: -1px;
  background: #ff8515;
  border: 1px solid white;
  border-radius: 2px;
  color: white;
  font-size: 0.7rem;
  padding: 2px 4px;
  margin: 0;
}

@media (min-height: 720px) {
  .video-item__info h2 {
    font-size: 0.9rem;
  }
  .video-item__thumbnail-lessons {
    font-size: 0.8rem;
  }
}
@media (min-height: 790px) {
  .video-item__info h2 {
    font-size: 1rem;
  }
  .video-item__thumbnail-lessons {
    font-size: 0.9rem;
  }
}
