.quiz-item {
  margin: 0;
  height: 100%;
  padding: 0;
}

.quiz-item__thumbnail {
  text-decoration: none;
  position: relative;
  display: block;
  outline: none;
  border: none;
  cursor: pointer;
  text-decoration: none;
  color: white;
  border: 1px solid #ff8515;
  border-radius: 0px 15px 1px;
  height: 100%;
  margin-right: 1rem;
  text-align: center;
  width: 50vw;
}
.quiz-item__thumbnail-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 30%;
  line-height: 1.2rem;
  padding: 1rem;
  margin: 0;
}

.quiz-item__thumbnail-title p {
  margin: 0;
  text-shadow: 0px 2px 8px rgba(0, 0, 0, 0.4);
  font-size: 1.4rem;
  font-weight: bold;
}

.quiz-item__thumbnail-matiere {
  position: absolute;
  left: 0;
  top: 0;
  padding: 0.1rem 0.6rem 0.2rem 0.4rem;
  color: white;
  background: #ff8515;
  border-bottom-right-radius: 10px;
}

.quiz-item__thumbnail-matiere p {
  margin: 0;
  font-size: 11px;
  line-height: 12px;
  text-align: center;
}

.quiz-item__thumbnail-questions {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 0.2rem 0.2rem 0.1rem 0.4rem;
  color: white;
  width: 90px;
  border-top-left-radius: 10px;
  background: #ff8515;
}

.quiz-item__thumbnail-questions p {
  margin: 0;
  padding: 0;
  text-align: center;
  line-height: 14px;
  font-size: 12px;
}

