.videos-list {
  padding: 0 1.3rem 0 0;
  width: 100%;
  margin: 0;
  list-style: none;
}

.bg-thumbnail-0 {
  background: url("../../img/bg-thumbnail-0.jpg") no-repeat center center /
    cover;
}
.bg-thumbnail-1 {
  background: url("../../img/bg-thumbnail-1.jpg") no-repeat center center /
    cover;
}
.bg-thumbnail-2 {
  background: url("../../img/bg-thumbnail-2.jpg") no-repeat center center /
    cover;
}

@media (min-width: 360px) {
  .videos-list {
    padding: 0 1.5rem 0 0;
  }
}
